<template>
  <MainContent id="camp-home" :style="{ 'background-image': 'url(' + backgroundImage + ')' }" >
    <SectionContent
        :title=section.title
        :subtitle=section.subtitle
        :contentText=section.contentText
        :rawHtml=section.rawHtml>
    </SectionContent>
  </MainContent>
</template>

<style>
#camp-home {
  background: no-repeat center;
  font-family: sans-serif;
  font-size: 1em;
  padding-bottom: 0.8em;
}

.content-text #camp-home {
  text-align: left;
  width: 80%;
  padding: 0.8em 0.8em 0.8em 0.8em;
  margin: auto;
  margin-top: 350px;
  margin-bottom: 0.8em;
  background-color: rgba(255,235,235,0.7);
}

@media screen and (min-width: 800px) {
  .content-text #camp-home {
    margin: auto;
    margin-top: 370px;
    margin-bottom: 0.8em;
  }
}
</style>

<script>
import MainContent from '@/components/common/MainContent.vue'
import SectionContent from '@/components/common/SectionContent.vue'

export default {
  name: 'Home',
  components: {
    MainContent,
    SectionContent
  },
  data () {
    return {
      height: '1000px',
      backgroundImage: '',
      backgroundImageSrc: require('@/assets/picture/camp/home/pc/main_visual.jpg'),
      backgroundImageSrcMobile: require('@/assets/picture/camp/home/mobile/main_visual.jpg'),
      section: {
        title: 'Camping Litchi',
        subtitle: '外で過ごす。それだけで楽しい。',
        contentText: '',
        rawHtml: '「どこにいようと、何をしていようと、迷惑をかけなければ勝手だろう」と言うのも何だか憚れるようになってきた気がする昨今。息苦しい日々に自由を見たのが<strong><ruby>野外泊<rp>(</rp><rt>キャンプ</rt><rp>)</rp></ruby></strong>。<br>もちろん、キャンプをするにもルールやマナーはあるし、山の中でも人の目はゼロではないから、気になることもあるかもしれない。だけどそれでも、ルールやマナーを守っている限り、不用意に干渉されることはない。<br>人付き合いに疲れやすい質なもんだから逃げてきた。って、身も蓋も無い言葉に感じられるけど、そんなに頻繁なわけでも無いから、許してもらえるとありがたい。'
      }
    }
  },
  methods: {
    handleResize: function () {
      this.height = window.innerHeight + 'px'
      if (window.innerWidth < 800) {
        if (window.innerHeight < 600) {
          this.height = '600px'
        }
        this.backgroundImage = this.backgroundImageSrcMobile
      } else {
        this.backgroundImage = this.backgroundImageSrc
      }
    }
  },
  created () {
    window.addEventListener('resize', this.handleResize)
  },
  unmounted () {
    window.removeEventListener('resize', this.handleResize)
  },
  mounted () {
    this.handleResize()
  }
}
</script>
